<template>
  <main class="enhanced-container">
    <!-- 卡片：商务信息填写（支持自增/删除字段） -->
    <section class="function-card">
      <div class="card-header">
        <span class="icon">🧾</span>
        <h2>商务信息填写</h2>
      </div>
      <div class="card-body">
        <div class="action-group">
          <!-- 动态字段列表 -->
          <div v-for="f in fields" :key="f.id" class="field-row">
            <div class="field-row-head">
              <label class="field-name">{{ f.label }}</label>
              <button class="mini-btn danger" type="button" @click="removeField(f.id)">删除</button>
            </div>
            <input
                class="modern-input"
                :type="f.type"
                v-model="f.value"
                :placeholder="`请输入${f.label}`"
            />
          </div>

          <!-- 添加字段区域 -->
          <div class="add-field">
            <h3 class="add-title">添加字段</h3>
            <div class="add-grid">
              <input class="modern-input" v-model="newField.label" placeholder="字段名（如：统一社会信用代码）" />
              <select class="modern-input" v-model="newField.type">
                <option value="text">文本</option>
                <option value="number">数字</option>
                <option value="date">日期</option>
                <option value="email">邮箱</option>
              </select>
              <input class="modern-input" :type="newField.type" v-model="newField.value" placeholder="可选：默认值" />
              <button class="gradient-btn success" type="button" @click="addField">添加</button>
            </div>
            <p v-if="fieldError" class="error-message">{{ fieldError }}</p>
          </div>
        </div>
      </div>
    </section>

    <!-- 卡片：文件上传与提交（提交后不等待后端，立即提示成功） -->
    <section class="function-card">
      <div class="card-header">
        <span class="icon">⬆️</span>
        <h2>文件上传与提交</h2>
      </div>
      <div class="card-body">
        <div class="upload-section">
          <label class="file-dropzone" @dragover.prevent @drop.prevent="onDrop">
            <div class="upload-content">
              <div class="upload-icon">📄</div>
              <div><strong>上传 DOCX 文件</strong>（仅限 .docx）</div>
              <div v-if="selectedFile">已选择：{{ selectedFile.name }}（{{ (selectedFile.size/1024).toFixed(1) }} KB）</div>
              <!-- 不等待后端响应，移除“正在提交”提示 -->
            </div>
            <input
                ref="fileInput"
                type="file"
                accept=".docx,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                @change="onFileChange"
            />
          </label>

          <!-- 信息串预览 -->
          <div class="preview-section">
            <div class="preview-header">
              <div>将发送的信息串</div>
              <div class="action-buttons">
                <button class="gradient-btn secondary" type="button" @click="copyInfoString" :disabled="!infoString">复制</button>
              </div>
            </div>
            <div class="preview-content">
              <pre v-if="infoString">{{ infoString }}</pre>
              <div v-else class="loading-message">当前无非空字段（允许为空）</div>
            </div>
          </div>

          <div class="action-buttons">
            <button class="gradient-btn secondary" type="button" @click="resetAll">重置</button>
            <button
                class="gradient-btn primary"
                type="button"
                @click="handleSubmit"
                :class="{ 'disabled-button': !selectedFile }"
                :disabled="!selectedFile"
            >
              提交
            </button>
          </div>

          <p v-if="error" class="error-message">{{ error }}</p>
          <p v-if="successMsg" class="success-message">{{ successMsg }}</p>
        </div>
      </div>
    </section>
  </main>
</template>

<script setup>
import { reactive, ref, computed } from 'vue'

/** ========= 动态字段：默认值与工具 ========= */
const uid = () => (Date.now().toString(36) + Math.random().toString(36).slice(2, 8))
const deepCopy = (arr) => arr.map(o => ({ ...o }))

// 默认字段与默认值（可按需调整）
const seedFields = [
  { id: uid(), label: '项目名称', type: 'text',  value: '' },
  { id: uid(), label: '项目编号', type: 'text',  value: '' },
  { id: uid(), label: '时间',     type: 'date',  value: '' },
  { id: uid(), label: '单位全称（报价方全称/报价供应商全称/投标人名称/申请人全称/投标单位企业名称/承诺方）', type: 'text',  value: '智联信通科技股份有限公司' },
  { id: uid(), label: '单位地址', type: 'text',  value: '山东省济宁市任城区税务街8号（济阳街道办事处综合楼二楼）' },
  { id: uid(), label: '开户银行', type: 'text',  value: '中国建设银行股份有限公司济宁金宇支行' },
  { id: uid(), label: '银行账号/开户行号', type: 'text', value: '37050168617109555666' },
  { id: uid(), label: '邮政编码', type: 'text',  value: '272000' },
  { id: uid(), label: '单位性质', type: 'text',  value: '股份有限公司（非上市、自然人投资或控股）' },
  { id: uid(), label: '成立时间', type: 'date',  value: '2005年04月01日' },
  { id: uid(), label: '经营期限', type: 'text',  value: '长期' },
  { id: uid(), label: '传真',     type: 'text',  value: '0537-3150005' },
  { id: uid(), label: '法人姓名', type: 'text',  value: '田常立' },
  { id: uid(), label: '性别',     type: 'text',  value: '男' },
  { id: uid(), label: '年龄',     type: 'number',value: '56' },
  { id: uid(), label: '职务',     type: 'text',  value: '董事长' },
  { id: uid(), label: '法人身份证号', type: 'text', value: '230103197605166839' },
  { id: uid(), label: '公司电话', type: 'text',  value: '0537-3150537' },
  { id: uid(), label: '电子邮箱', type: 'email', value: 'zlxtkjgf@163.com' }
]

const fields = ref(deepCopy(seedFields))
const fieldError = ref('')
const newField = reactive({ label: '', type: 'text', value: '' })

// ========== 文件上传状态 ==========
const selectedFile = ref(null)
const fileInput = ref(null)
const error = ref('')
const successMsg = ref('')

// ========== 信息串（仅拼接非空值） ==========
const infoString = computed(() => {
  return fields.value
      .map(f => {
        let val = String(f.value ?? '').trim()
        if (!val) return ''
        // 如果是 date 类型，格式化成年月日
        if (f.type === 'date') {
          // 只处理合法日期
          const d = new Date(val)
          if (!isNaN(d)) {
            const yyyy = d.getFullYear()
            const mm = String(d.getMonth() + 1).padStart(2, '0')
            const dd = String(d.getDate()).padStart(2, '0')
            val = `${yyyy}年${mm}月${dd}日`
          }
        }
        return `${f.label}：${val}`
      })
      .filter(Boolean)
      .join('\n')
})


// ========== 字段操作 ==========
function addField() {
  fieldError.value = ''
  const label = (newField.label || '').trim()
  if (!label) { fieldError.value = '请填写字段名'; return }
  if (fields.value.some(f => f.label === label)) { fieldError.value = '已存在同名字段'; return }
  fields.value.push({ id: uid(), label, type: newField.type, value: newField.value })
  newField.label = ''
  newField.type = 'text'
  newField.value = ''
}
function removeField(id) {
  fields.value = fields.value.filter(f => f.id !== id)
}

// ========== 上传交互 ==========
function onDrop(e) {
  const f = e.dataTransfer?.files?.[0]
  if (!f) return
  onFileChange({ target: { files: [f] } })
}
function onFileChange(e) {
  error.value = ''
  successMsg.value = ''
  const file = e.target.files?.[0]
  if (!file) { selectedFile.value = null; return }
  if (file.size > 5 * 1024 * 1024) { error.value = '文件大小不能超过 5MB'; clearFile(); return }
  const isDocx = file.type === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document' || /\.docx$/i.test(file.name)
  if (!isDocx) { error.value = '仅支持 .docx 文件'; clearFile(); return }
  selectedFile.value = file
}
function clearFile() {
  selectedFile.value = null
  if (fileInput.value) fileInput.value.value = ''
}

// ========== 提交：不等待后端响应，直接提示成功 ==========
function handleSubmit() {
  error.value = ''
  successMsg.value = ''
  if (!selectedFile.value) { error.value = '请先选择 .docx 文件'; return }

  const fd = new FormData()
  fd.append('file', selectedFile.value)
  fd.append('info', infoString.value || '')

  // fire-and-forget，不等待响应
  submitToUploadBs(fd)

  // 立即提示成功并清空文件选择
  successMsg.value = '提交成功！请等待一段时间之后前往预览页面查看生成结果。'
  clearFile()

  // 如需自动跳转到预览页，请取消下一行注释并替换路由
  // router.push({ name: 'Preview' })
}
function submitToUploadBs(formData) {
  fetch('/api/BidGeneration/uploadBs', { method: 'POST', body: formData })
      .then(resp => {
        if (!resp.ok) console.error('上传失败：', resp.status, resp.statusText)
      })
      .catch(err => console.error('上传异常：', err))
}

async function copyInfoString() {
  if (!infoString.value) return
  try {
    if (navigator.clipboard?.writeText) {
      await navigator.clipboard.writeText(infoString.value)
    } else {
      const ta = document.createElement('textarea')
      ta.value = infoString.value
      ta.style.position = 'fixed'
      ta.style.left = '-9999px'
      document.body.appendChild(ta)
      ta.select()
      document.execCommand('copy')
      document.body.removeChild(ta)
    }
    successMsg.value = '已复制到剪贴板'
  } catch (err) {
    error.value = '复制失败，请手动选择并复制'
  } finally {
    setTimeout(() => (successMsg.value = ''), 2000)
  }
}

function resetAll() {
  fields.value = deepCopy(seedFields)
  error.value = ''
  successMsg.value = ''
  clearFile()
}
</script>

<style scoped>
