.add-grid { display: grid; grid-template-columns: 2fr 1fr 2fr auto; gap: 12px; }
@media (max-width: 768px) { .add-grid { grid-template-columns: 1fr; } }

.mini-btn {
  border: none; background: none; cursor: pointer; padding: 4px 8px; border-radius: 6px; font-size: .9rem;
}
.mini-btn:hover { background: #f5f5f5; }
.mini-btn.danger { color: #dc3545; }

