.preview-section { border: 1px solid #eee; border-radius: 14px; overflow: hidden; }
.preview-header { display: flex; justify-content: space-between; align-items: center; padding: 1rem; background: #f8f9fa; border-bottom: 1px solid #eee; flex-wrap: wrap; }
.preview-content { padding: 1.5rem; max-height: 500px; overflow-y: auto; }

.action-buttons { display: flex; gap: 12px; flex-wrap: wrap; }
.error-message { color: #dc3545; font-weight: 500; margin-top: 8px; }
.success-message { margin-top: 8px; color: #28a745; font-weight: 600; }

@media (max-width: 768px) {
  .gradient-btn { width: 100%; justify-content: center; }
  .card-header { flex-direction: column; align-items: flex-start; }
}
