<template>
  <div class="file-manager">
    <h2>商务文件下载</h2>

    <div class="filter-container">
      <label for="category-select">选择类别：</label>
      <select id="category-select" v-model="selectedCategory">
        <option value="">全部</option>
        <option v-for="cat in categories" :key="cat" :value="cat">{{ cat }}</option>
      </select>
      <label style="margin-left:16px; display:flex; align-items:center; gap:6px;">
        <input type="checkbox" v-model="onlyWord" /> 仅显示 Word（.doc/.docx）
      </label>
    </div>

    <table class="file-table">
      <thead>
      <tr>
        <th>序号</th>
        <th>文档名称</th>
        <th>上传时间</th>
        <th>到期时间</th>
        <th>操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(file, index) in filteredFiles" :key="file.filePath || file.materialName">
        <td>{{ index + 1 }}</td>
        <td class="file-name">{{ file.materialName }}</td>
        <td>{{ formatDate(file.uploadTime) }}</td>
        <td>{{ formatDate(file.expiryDate) }}</td>
        <td>
          <button
              @click="downloadFile(file)"
              class="btn-edit"
              :disabled="downloadingPath===file.filePath"
          >
            <span v-if="downloadingPath===file.filePath">下载中…</span>
            <span v-else>下载</span>
          </button>

          <button
              @click="deleteFile(file)"
              class="btn-delete"
              :disabled="deletingPath===file.filePath"
              title="删除后不可恢复"
          >
            <span v-if="deletingPath===file.filePath">删除中…</span>
            <span v-else>删除</span>
          </button>
        </td>
      </tr>
      <tr v-if="filteredFiles.length === 0">
        <td colspan="5" class="no-data">暂无文件</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'BusinessWordDownloader',
  props: {
    // 列表接口：支持直接返回数组，也支持 { materials: [...] }
    listApi: { type: String, default: '/api/businessTender/getAll' },
    // 下载接口：默认拼接 ?filePath=
    downloadApi: { type: String, default: '/api/materials/download' },
    // 删除接口：DELETE /api/businessTender/delete/{fileName}
    deleteApi: { type: String, default: '/api/businessTender/delete' },
  },
  data() {
    return {
      files: [],
      categories: [],
      selectedCategory: '',
      onlyWord: true,
      downloadingPath: null,
      deletingPath: null,
    };
  },
  computed: {
    filteredFiles() {
      let list = this.files;
      if (this.selectedCategory) {
        list = list.filter(f => f.category === this.selectedCategory);
      }
      if (this.onlyWord) {
        list = list.filter(f => this.isWordPath(f.filePath || f.materialName));
      }
      return list;
    },
  },
  created() {
    this.fetchFiles();
  },
  methods: {
    // —— 数据获取 & 归一化 —— //
    async fetchFiles() {
      try {
        const resp = await axios.get(this.listApi, { withCredentials: true });
        const raw = Array.isArray(resp.data)
            ? resp.data
            : (Array.isArray(resp.data?.materials) ? resp.data.materials : []);

        this.files = raw.map(it => {
          const path = it.filePath || '';
          const name = it.fileName || this.basename(path) || it.materialName || '';
          return {
            ...it,
            filePath: path,
            materialName: name,                 // 用于显示 & 删除用的 {fileName}
            uploadTime: it.creatTime || it.uploadTime || null,
            expiryDate: it.expiryDate || null,
            category: this.parentDir(path) || '',
          };
        });

        this.extractCategories();
      } catch (err) {
        console.error('获取文件列表失败：', err);
        this.files = [];
        this.categories = [];
      }
    },

    extractCategories() {
      const set = new Set(this.files.map(f => f.category).filter(Boolean));
      this.categories = Array.from(set);
    },

    basename(p = '') {
      const segs = p.split('/').filter(Boolean);
      return segs[segs.length - 1] || '';
    },
    parentDir(p = '') {
      const segs = p.split('/').filter(Boolean);
      return segs.length >= 2 ? segs[segs.length - 2] : '';
    },

    // —— 工具方法 —— //
    isWordPath(p = '') {
      const lower = (p || '').toLowerCase();
      return lower.endsWith('.doc') || lower.endsWith('.docx');
    },

    formatDate(isoString) {
      if (!isoString) return '-';
      const d = new Date(isoString);
      if (isNaN(d)) return '-';
      const YYYY = d.getFullYear();
      const MM = String(d.getMonth() + 1).padStart(2, '0');
      const DD = String(d.getDate()).padStart(2, '0');
      const hh = String(d.getHours()).padStart(2, '0');
      const mm = String(d.getMinutes()).padStart(2, '0');
      return `${YYYY}-${MM}-${DD} ${hh}:${mm}`;
    },

    // —— 下载 —— //
    downloadUrl(filePath) {
      return `${this.downloadApi}?filePath=${encodeURIComponent(filePath)}`;
    },
    toAbsolute(url) {
      try { return new URL(url, window.location.origin).href; } catch { return url; }
    },
    parseFileNameFromCD(cd) {
      if (!cd) return '';
      const m = /filename\*=UTF-8''([^;]+)|filename=\"?([^\";]+)\"?/i.exec(cd);
      try { return decodeURIComponent((m?.[1] || m?.[2] || '').trim()); } catch { return (m?.[1] || m?.[2] || '').trim(); }
    },

    async downloadFile(file) {
      const filePath = file.filePath || file.materialName;
      if (!filePath) return;
      const url = this.downloadUrl(filePath);
      this.downloadingPath = filePath;
      try {
        const resp = await axios.get(url, { responseType: 'blob', withCredentials: true });
        const cd = resp.headers['content-disposition'] || resp.headers['Content-Disposition'];
        const fromHeader = this.parseFileNameFromCD(cd);
        const fallback = (file.materialName || filePath).split('/').pop();
        const fileName = fromHeader || fallback || '未命名.docx';

        const blobUrl = URL.createObjectURL(resp.data);
        const a = document.createElement('a');
        a.href = blobUrl;
        a.download = fileName;
        document.body.appendChild(a);
        a.click();
        a.remove();
        URL.revokeObjectURL(blobUrl);
      } catch (err) {
        console.warn('Blob 下载失败，尝试打开直链…', err);
        window.open(this.toAbsolute(url), '_blank', 'noopener');
      } finally {
        this.downloadingPath = null;
      }
    },

    // —— 删除（DELETE /api/businessTender/delete/{fileName}）—— //
    async deleteFile(file) {
      // 按后端要求用“文件名”作为路径参数
      const fileName = file.materialName || this.basename(file.filePath || '');
      if (!fileName) return;

      if (!confirm('确认删除该文件吗？此操作不可恢复')) return;

      // 用 filePath 标记禁用状态，UI 体验更直观
      const key = file.filePath || file.materialName;
      this.deletingPath = key;

      try {
        const url = `${this.deleteApi}/${encodeURIComponent(fileName)}`;
        await axios.delete(url, { withCredentials: true });

        // 本地移除（以 filePath 为主键；没有则回退到 materialName）
        this.files = this.files.filter(
            f => (f.filePath || f.materialName) !== key
        );
        this.extractCategories();
      } catch (err) {
        const msg = err?.response?.data?.message || err?.message || '删除失败';
        alert(msg);
      } finally {
        this.deletingPath = null;
      }
    },
  },
};
</script>

<style scoped>
.file-manager { max-width: 900px; margin: 20px auto; padding: 0 16px; font-family: "Helvetica Neue", Arial, sans-serif; }
h2 { text-align: center; margin-bottom: 16px; }
.filter-container { margin-bottom: 12px; display: flex; align-items: center; }
.filter-container label { margin-right: 8px; font-weight: bold; }
.filter-container select { padding: 4px 8px; border: 1px solid #ccc; border-radius: 4px; }
.file-table { width: 100%; border-collapse: collapse; margin-bottom: 24px; }
.file-table th, .file-table td { border: 1px solid #ddd; padding: 8px 12px; text-align: left; }
.file-table th { background-color: #f5f5f5; }
.file-table tr:nth-child(even) { background-color: #fafafa; }
.file-table tr:hover { background-color: #f1f1f1; }
.no-data { text-align: center; color: #888; padding: 20px 0; }
.file-name { word-break: break-all; }
.btn-edit, .btn-delete { border: none; padding: 6px 12px; margin-right: 8px; cursor: pointer; border-radius: 4px; font-size: 14px; transition: background 0.2s; }
.btn-edit { background-color: #2196f3; color: white; }
.btn-edit:hover { background-color: #1976d2; }
.btn-delete { background-color: #f44336; color: white; }
.btn-delete:hover { background-color: #d32f2f; }
</style>
