  border-radius: 6px;
  border: 1px solid #e0e0e0;
  background: #f7f7f7;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: background .2s, border-color .2s, transform .08s ease;
  font-size: 1.2rem;
  user-select: none;
}

