/* 进度条 */
.arrow-progress { display: flex; justify-content: space-between; align-items: center; margin: 2rem auto; max-width: 600px; }
.step-container { display: flex; align-items: center; flex: 1; }
.step { display: flex; flex-direction: column; align-items: center; position: relative; z-index: 1; }
.circle { width: 36px; height: 36px; border-radius: 50%; background: #e0e0e0; display: flex; align-items: center; justify-content: center; font-weight: bold; color: #666; transition: all 0.3s ease; }
.step.active .circle { background: #4a90e2; color: white; }
.arrow { flex: 1; height: 3px; background: #e0e0e0; position: relative; margin: 0 -2px; transition: all 0.3s ease; }
.arrow::after { content: ''; position: absolute; right: -6px; top: -3px; border: 6px solid transparent; border-left-color: #e0e0e0; }
.arrow.active { background: #4a90e2; }
.arrow.active::after { border-left-color: #4a90e2; }
