.modern-input:focus { outline: none; border-color: #4a90e2; }

/* 预览主体 */
.preview-main { border: 1px solid #e0e0e0; border-radius: 8px; overflow: hidden; display: flex; flex-direction: column; box-sizing: border-box; }
