.editor-title { margin: 0; font-size: 1.2rem; color: #333; }
.editor-tools { display: flex; gap: 0.75rem; }
.tool-btn { padding: 0.5rem 1rem; border: 1px solid #e0e0e0; border-radius: 6px; background-color: white; cursor: pointer; transition: all 0.2s; font-size: 0.9rem; }
.tool-btn:hover:not(:disabled) { background-color: #f5f5f5; border-color: #d0d0d0; }
.tool-btn:disabled { background-color: #f9f9f9; color: #999; cursor: not-allowed; }

.editor-content { flex-grow: 1; min-height: 300px; padding: 1.5rem; overflow-y: auto; box-sizing: border-box; max-width: 1000px; margin: 0 auto; width: 100%; }

/* 预览（直接渲染 HTML） */
.markdown-preview { line-height: 1.8; color: #333; }
.markdown-preview h1, .markdown-preview h2, .markdown-preview h3 { margin-top: 1.5rem; margin-bottom: 1rem; color: #222; border-bottom: 1px solid #eee; padding-bottom: 0.5rem; }
.markdown-preview p { margin-bottom: 1.5rem; line-height: 1.8; white-space: normal; }
.markdown-preview ul, .markdown-preview ol { margin-bottom: 1.5rem; padding-left: 2.2rem; }
.markdown-preview li { margin-bottom: 0.8rem; line-height: 1.8; }
.markdown-preview img { max-width: 100%; border-radius: 8px; margin: 1rem 0; }
