.markdown-preview pre { background-color: #f5f5f5; padding: 1rem; border-radius: 8px; overflow-x: auto; margin-bottom: 1.5rem; }

/* 状态提示与按钮区 */
.status-message { padding: 0.75rem 1.5rem; margin: 1rem; border-radius: 6px; font-size: 0.9rem; text-align: center; }
.status-message.success { background-color: #f0fff4; color: #28a745; border: 1px solid #c3e6c3; }
