<template>
  <div class="employee-manager">
    <h2>人员管理</h2>

    <!-- 职位下拉菜单 -->
    <div class="filter-container" v-if="positions.length">
      <label for="position-select">选择职位：</label>
      <select id="position-select" v-model="selectedPosition">
        <option value="">全部</option>
        <option v-for="pos in positions" :key="pos" :value="pos">
          {{ pos }}
        </option>
      </select>
    </div>

    <!-- 人员列表表格 -->
    <table class="employee-table">
      <thead>
      <tr>
        <th>序号</th>
        <th>姓名</th>
        <th>职位</th>
        <th>操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(emp, index) in filteredEmployees" :key="emp.id">
        <td>{{ index + 1 }}</td>
        <td class="emp-name">{{ emp.name }}</td>
        <td>{{ emp.position }}</td>
        <td>
          <button @click="viewEmployee(emp)" class="btn-view">查看</button>
          <button @click="editEmployee(emp)" class="btn-edit">修改</button>
          <button @click="deleteEmployee(emp)" class="btn-delete">删除</button>
        </td>
      </tr>
      <tr v-if="filteredEmployees.length === 0">
        <td colspan="4" class="no-data">暂无人员信息</td>
      </tr>
      </tbody>
    </table>

    <!-- 人员详情弹窗（仅展示 id、name、position） -->
    <div
        v-if="showDetail"
        class="detail-overlay"
        @click.self="closeDetail"
    >
      <div class="detail-container">
        <div class="detail-header">
          <span>人员详情：{{ currentEmployee.name }}</span>
          <button @click="closeDetail" class="close-btn">×</button>
        </div>
        <div class="detail-body">
          <p><strong>ID：</strong> {{ currentEmployee.id }}</p>
          <p><strong>姓名：</strong> {{ currentEmployee.name }}</p>
          <p><strong>职位：</strong> {{ currentEmployee.position }}</p>
        </div>
      </div>
    </div>

    <!-- 删除确认提示 -->
    <div
        v-if="showConfirm"
        class="confirm-overlay"
        @click.self="showConfirm = false"
    >
      <div class="confirm-box">
        <p>确认要删除 “{{ employeeToDelete.name }}” 吗？</p>
        <div class="confirm-actions">
          <button @click="confirmDelete" :disabled="deleting" class="btn-confirm-yes">
            {{ deleting ? "删除中…" : "确认" }}
          </button>
          <button @click="showConfirm = false" :disabled="deleting" class="btn-confirm-no">
            取消
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "EmployeeManager",
  data() {
    return {
      employees: [],            // { id, name, position } 列表
      positions: [],            // 去重后的职位列表
      selectedPosition: "",     // 当前选中的职位，空字符串代表“全部”
      showDetail: false,
      currentEmployee: {},
      showConfirm: false,
      employeeToDelete: {},
      deleting: false
    };
  },
  computed: {
    // 根据 selectedPosition 过滤员工列表
    filteredEmployees() {
      if (!this.selectedPosition) {
        return this.employees;
      }
      return this.employees.filter(
          (emp) => emp.position === this.selectedPosition
      );
    }
  },
  created() {
    this.fetchEmployees();
  },
  methods: {
    // 从后端 GET /employees/all 拉取所有员工
    async fetchEmployees() {
      try {
        // 如果实际接口带 /api 前缀，请改为 "/api/employees/all"
        const resp = await axios.get("/api/employees/all");
        const arr = Array.isArray(resp.data) ? resp.data : [];
        // 只映射 id, name, position 三个字段
        this.employees = arr.map((item) => ({
          id: item.employeeID,
          name: item.employeesName,
          position: item.employeesPosition
        }));
        this.extractPositions();
      } catch (err) {
        console.error("获取人员列表失败：", err);
        this.employees = [];
        this.positions = [];
      }
    },

    // 提取所有不重复的 position 字段
    extractPositions() {
      const set = new Set();
      this.employees.forEach((emp) => {
        if (emp.position) {
          set.add(emp.position);
        }
      });
      this.positions = Array.from(set);
    },

    // 点击“查看”时，弹出详情弹窗
    viewEmployee(emp) {
      this.currentEmployee = emp;
      this.showDetail = true;
    },

    // 关闭详情弹窗
    closeDetail() {
      this.showDetail = false;
      this.currentEmployee = {};
    },

    // 点击“删除”按钮，弹出确认框
    deleteEmployee(emp) {
      this.employeeToDelete = emp;
      this.showConfirm = true;
    },

    // 确认删除后，调用后端 DELETE /employees/delete/{id} 并刷新列表
    async confirmDelete() {
      if (!this.employeeToDelete.id) return;
      this.deleting = true;
      try {
        // 如果接口需加前缀 /api，请改为 `/api/employees/delete/${id}`
        await axios.delete(`/api/employees/delete/${this.employeeToDelete.id}`);
        this.showConfirm = false;
        this.employeeToDelete = {};
        await this.fetchEmployees();
      } catch (err) {
        console.error("删除员工失败：", err);
        alert(err.response?.data?.message || "删除失败，请稍后重试");
      } finally {
        this.deleting = false;
      }
    },

    // 点击“修改”按钮，调用 prompt 修改职位
    async editEmployee(emp) {
      const newPosition = prompt(
          `请输入 ${emp.name} 的新职位：`,
          emp.position || ""
      );
      if (!newPosition || newPosition.trim() === "" || newPosition === emp.position) {
        return;
      }
      try {
        // 假设后端更新接口为 PUT /employees/update?id={id}&position={newPosition}
        await axios.put("/api/employees/update", null, {
          params: {
            id: emp.id,
            position: newPosition.trim()
          }
        });
        await this.fetchEmployees();
        alert("修改成功");
      } catch (err) {
        console.error("修改失败：", err);
        alert("修改失败，请稍后重试");
      }
    }
  }
};
</script>

