<template>
  <div class="kb-upload">
    <h2>知识库上传</h2>

    <!-- 类别（= 文件夹名），按后端签名 folderName 发送 -->
    <section class="cat-card">
      <div class="cat-head">
        <span class="icon">📂</span>
        <h3>类别名（使用文件夹名）</h3>
      </div>
      <div class="cat-body">
        <div class="cat-create">
          <input
              class="modern-input"
              :value="folderNameInput"
              @input="onFolderNameInput"
              :disabled="isUploading"
              placeholder="例如：产品手册或直接使用所选文件夹名"
          />
          <button class="btn ghost" @click="useDetectedRoot" :disabled="!rootFolder || isUploading">用检测到的：{{ rootFolder || '—' }}</button>
        </div>
        <div class="cat-empty note">提示：默认会从所选/拖入的文件路径中，自动识别首个公共目录作为类别名。</div>
      </div>
    </section>

    <!-- 选择文件夹 + 过滤/选项 -->
    <div class="picker-row">
      <label class="btn">
        选择文件夹
        <input
            type="file"
            webkitdirectory
            multiple
            @change="onFolderInputChange"
            style="display:none"
        />
      </label>

      <button class="btn ghost" @click="reset" :disabled="isUploading || !files.length">
        清空选择
      </button>

      <label class="opt">
        <input type="checkbox" v-model="filterByExt" :disabled="isUploading" />
        仅允许常见文档类型
      </label>
    </div>

    <!-- 拖拽区域（支持整个文件夹拖入） -->
    <div
        class="dropzone"
        :class="{ dragging }"
        @dragover.prevent="onDragOver"
        @dragleave.prevent="onDragLeave"
        @drop.prevent="onDrop"
    >
      <div class="dz-inner">
        <strong>拖拽文件夹或文件到此处</strong>
        <div class="sub">也可以点击上方按钮选择整个文件夹</div>
        <div class="hint">将保留相对路径（如 docs/ch1/intro.md）</div>
      </div>
    </div>

    <!-- 选择预览 -->
    <div v-if="files.length" class="summary">
      <div><b>自动检测到的根目录：</b>{{ rootFolder || '（未能统一检测）' }}</div>
      <div><b>上传到的类别名（folderName）：</b>{{ folderNameInput || '（未填写）' }}</div>
      <div><b>文件数：</b>{{ files.length }}</div>
      <div><b>总大小：</b>{{ humanSize(totalBytes) }}</div>
      <div><b>模式：</b>{{ uploadModeLabel }}</div>
    </div>

    <div v-if="files.length" class="list">
      <div class="list-head">
        <div style="padding-left:6px">相对路径</div>
        <div>状态</div>
        <div class="right">进度</div>
        <div class="right">大小</div>
        <div class="right">操作</div>
      </div>
      <div class="list-body">
        <div v-for="(it, i) in files" :key="it.key" class="row">
          <div class="path" :title="it.relPath">{{ it.relPath }}</div>
          <div class="status">
            <span v-if="it.status==='queued'">待上传</span>
            <span v-else-if="it.status==='uploading'">上传中</span>
            <span v-else-if="it.status==='done'" class="ok">完成</span>
            <span v-else-if="it.status==='error'" class="err">失败</span>
          </div>
          <div class="right small">{{ perFilePercent(it).toFixed(0) }}%</div>
          <div class="size">{{ humanSize(it.size) }}</div>
          <div class="right">
            <button class="lnk" @click="removeOne(i)" :disabled="isUploading">移除</button>
          </div>
        </div>
      </div>
    </div>

    <!-- 上传控制 -->
    <div class="actions">
      <button class="btn primary" @click="startUpload" :disabled="!files.length || isUploading || !folderNameInput.trim()">
        {{ isUploading ? '正在上传…' : '开始上传' }}
      </button>
      <button class="btn danger" v-if="isUploading" @click="cancelUpload">取消上传</button>
    </div>

    <!-- 进度条（总） -->
    <div v-if="isUploading || progress.totalBytes > 0" class="progress">
      <div class="bar" :style="{ width: progressPercent + '%' }"></div>
      <div class="text">
        {{ progressPercent.toFixed(1) }}%（{{ humanSize(progress.loadedBytes) }} / {{ humanSize(progress.totalBytes || totalBytes) }}）
      </div>
    </div>

    <!-- 提示信息 -->
    <p v-if="message" class="msg" :class="messageType">{{ message }}</p>
  </div>
</template>

<script setup>
import { ref, computed, nextTick } from 'vue'

/**
 * 后端签名：
 * POST /api/KnowledgeBase/upload
 * @RequestParam("folderName") String folderName,
 * @RequestPart("files") MultipartFile[] files
 */
const CFG = {
  uploadEndpoint: '/api/KnowledgeBase/upload',
  perFileConcurrency: 3,
  batchThresholdBytes: 80 * 1024 * 1024, // >80MB 时逐文件并发上传
  acceptExt: ['.txt', '.md', '.pdf', '.doc', '.docx', '.xls', '.xlsx', '.ppt', '.pptx', '.csv', '.json', '.yaml', '.yml'],
  excludePatterns: [/\/\.git(\/|$)/i, /\/node_modules(\/|$)/i, /\/(\.DS_Store|Thumbs\.db)(\/|$)/i]
}

// 状态
const files = ref([]) // { key, file, relPath, size, status, loaded, total }
const rootFolder = ref('')           // 自动识别的公共首级目录
const folderNameInput = ref('')      // 发送给后端的 folderName
const userEditedFolderName = ref(false)
const dragging = ref(false)

const isUploading = ref(false)
let abortAll = null

const progress = ref({ loadedBytes: 0, totalBytes: 0 })
const message = ref('')
const messageType = ref('info')

const filterByExt = ref(false)

// 计算
const totalBytes = computed(() =>
    files.value.reduce((s, it) => s + (it.size || 0), 0)
)
const progressPercent = computed(() => {
  const total = progress.value.totalBytes || totalBytes.value || 1
  return Math.min(100, (progress.value.loadedBytes / total) * 100)
})
const uploadModeLabel = computed(() =>
    totalBytes.value > CFG.batchThresholdBytes ? '逐文件并发上传' : '表单批量上传'
)

// 工具函数
function humanSize(bytes) {
  if (bytes === undefined || bytes === null) return '-'
  const units = ['B', 'KB', 'MB', 'GB', 'TB']
  let i = 0; let num = bytes
  while (num >= 1024 && i < units.length - 1) { num /= 1024; i++ }
  return `${num.toFixed(num >= 100 || i === 0 ? 0 : 1)} ${units[i]}`
}
function setMessage(text, type = 'info') { message.value = text; messageType.value = type }
function resetMessages() { setMessage('', 'info') }

function perFilePercent(it) {
  if (it.total > 0) return (it.loaded / it.total) * 100
  if (it.status === 'done') return 100
  return 0
}

function extOf(path) {
  path = String(path || '')
  // 扩展名匹配：末尾的 .xxx，禁止包含 / 或 \
  const m = /\.([^.\/\\]+)$/.exec(path)
  return m ? `.${m[1].toLowerCase()}` : ''
}

function normalizeRel(p) {
  return String(p || '')
      .replace(/\\/g, '/')   // 反斜杠 => 正斜杠
      .replace(/^\/+/, '')   // 去掉前导斜杠
}

function deriveRootFromRelPaths() {
  const rels = files.value.map(f => f.relPath)
  if (!rels.length) return
  const firstSegs = rels
      .map(p => normalizeRel(p).split('/').filter(Boolean)[0] || '')
  const same = firstSegs.every(s => s && s === firstSegs[0])
  rootFolder.value = same ? firstSegs[0] : ''
  if (!userEditedFolderName.value) folderNameInput.value = rootFolder.value || ''
}

function onFolderNameInput(e) {
  folderNameInput.value = e.target.value
  userEditedFolderName.value = true
}
function useDetectedRoot() {
  folderNameInput.value = rootFolder.value || ''
  userEditedFolderName.value = true
}

// 选择 & 拖拽
function onFolderInputChange(e) {
  reset()
  const inputFiles = Array.from(e.target.files || [])
  if (!inputFiles.length) return
  const items = inputFiles.map(f => ({ file: f, relPath: f.webkitRelativePath || f.name }))
  applyCollected(items)
}
function onDragOver() { dragging.value = true }
function onDragLeave() { dragging.value = false }

async function onDrop(e) {
  dragging.value = false
  reset()

  const dt = e.dataTransfer
  const entries = []
  if (dt.items && dt.items.length) {
    for (const item of dt.items) {
      const entry = item.webkitGetAsEntry ? item.webkitGetAsEntry() : null
      if (entry) entries.push(entry)
    }
  }

  let collected = []
  if (entries.length) {
    for (const entry of entries) {
      const sub = await traverseEntry(entry, '')
      collected = collected.concat(sub)
    }
  } else {
    collected = Array.from(dt.files || []).map(f => ({
      file: f,
      relPath: normalizeRel(f.webkitRelativePath || f.name || '')
    }))
  }
  applyCollected(collected)
}

async function traverseEntry(entry, prefix) {
  if (entry.isFile) {
    const file = await new Promise((res, rej) => entry.file(res, rej))
    return [{ file, relPath: (prefix ? `${prefix}/` : '') + entry.name }]
  }
  if (entry.isDirectory) {
    const dirReader = entry.createReader()
    const all = []
    async function readAll() {
      const batch = await new Promise((res, rej) => dirReader.readEntries(res, rej))
      if (!batch.length) return
      for (const ent of batch) {
        const sub = await traverseEntry(ent, (prefix ? `${prefix}/` : '') + entry.name)
        all.push(...sub)
      }
      await readAll()
    }
    await readAll()
    return all
  }
  return []
}

function applyCollected(collected) {
  // 过滤
  collected = collected.filter(it => !CFG.excludePatterns.some(p => p.test(it.relPath)))
  if (filterByExt.value) {
    const allow = new Set(CFG.acceptExt.map(e => e.toLowerCase()))
    collected = collected.filter(it => allow.has(extOf(it.relPath)))
  }

  // 去重（按相对路径）
  const map = new Map()
  for (const it of collected) {
    const key = normalizeRel(it.relPath)
    if (!map.has(key)) map.set(key, it)
  }

  const items = Array.from(map.entries()).map(([p, { file }]) => ({
    key: `${p}#${file.lastModified}-${file.size}`,
    file,
    relPath: p,
    size: file.size || 0,
    status: 'queued',
    loaded: 0,
    total: file.size || 0
  }))

  // 排序
  items.sort((a, b) => a.relPath.localeCompare(b.relPath))

  files.value = items
  deriveRootFromRelPaths()
}

// 上传
function buildFormData(all = true, singleFile) {
  const fd = new FormData()
  if (all) {
    for (const it of files.value) {
      fd.append('files', it.file, it.relPath)
    }
  } else if (singleFile) {
    fd.append('files', singleFile.file, singleFile.relPath)
  }
  fd.append('folderName', (folderNameInput.value || '').trim())
  return fd
}

async function startUpload() {
  if (!files.value.length || isUploading.value) return
  if (!folderNameInput.value.trim()) {
    setMessage('请先填写类别名（folderName），或点击“用检测到的”', 'error')
    return
  }
  resetMessages()

  progress.value.totalBytes = totalBytes.value
  progress.value.loadedBytes = 0
  isUploading.value = true
  abortAll = () => {}

  try {
    if (totalBytes.value > CFG.batchThresholdBytes) {
      await uploadPerFileMode()
    } else {
      await uploadBatchMode()
    }
    setMessage('上传完成。', 'ok')
  } catch (err) {
    console.error(err)
    setMessage('上传过程中出现错误。', 'error')
  } finally {
    isUploading.value = false
    abortAll = null
  }
}

function cancelUpload() {
  if (abortAll) abortAll()
  isUploading.value = false
  setMessage('已取消上传。', 'info')
}

async function uploadBatchMode() {
  files.value.forEach(it => { it.status = 'uploading'; it.loaded = 0; it.total = it.size })

  await new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest()
    abortAll = () => { try { xhr.abort() } catch { /* ignore */ } }

    xhr.open('POST', CFG.uploadEndpoint, true)

    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) progress.value.totalBytes = e.total
      progress.value.loadedBytes = e.loaded
    }
    xhr.onerror = () => reject(new Error('网络或跨域错误'))
    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 300) {
        files.value.forEach(it => { it.status = 'done'; it.loaded = it.total })
        try {
          const res = JSON.parse(xhr.responseText || '{}')
          if (res && res.message) setMessage(`上传成功：${res.message}`, 'ok')
        } catch { /* ignore */ }
        resolve()
      } else {
        files.value.forEach(it => { if (it.status !== 'done') it.status = 'error' })
        reject(new Error(`服务器错误 ${xhr.status}`))
      }
    }

    xhr.send(buildFormData(true))
  })
}

async function uploadPerFileMode() {
  progress.value.loadedBytes = 0

  const controllers = new Set()
  abortAll = () => { controllers.forEach(c => c.abort && c.abort()) }

  const queue = files.value.slice()
  queue.forEach(it => { it.status = 'queued'; it.loaded = 0; it.total = it.size })

  let inFlight = 0
  let idx = 0

  await new Promise((resolve, reject) => {
    const next = () => {
      if (!isUploading.value) { reject(new Error('已取消')); return }
      while (inFlight < CFG.perFileConcurrency && idx < queue.length) {
        const it = queue[idx++]
        inFlight++
        uploadOne(it, controllers)
            .then(() => {
              inFlight--
              if (inFlight === 0 && idx >= queue.length) resolve()
              else next()
            })
            .catch(err => {
              inFlight--
              it.status = 'error'
              console.error('单文件失败:', it.relPath, err)
              next()
            })
      }
      if (inFlight === 0 && idx >= queue.length) resolve()
    }
    next()
  })
}

function uploadOne(it, controllers) {
  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest()
    it.status = 'uploading'
    it.loaded = 0

    xhr.open('POST', CFG.uploadEndpoint, true)

    const controller = { abort: () => { try { xhr.abort() } catch { /* ignore */ } } }
    controllers.add(controller)

    xhr.upload.onprogress = (e) => {
      const prev = it.loaded
      it.total = e.lengthComputable ? e.total : it.size
      it.loaded = e.loaded
      progress.value.loadedBytes += Math.max(0, it.loaded - prev)
    }

    xhr.onerror = () => { controllers.delete(controller); reject(new Error('网络错误')) }
    xhr.onload = () => {
      controllers.delete(controller)
      if (xhr.status >= 200 && xhr.status < 300) {
        it.status = 'done'
        it.loaded = it.total
        resolve()
      } else {
        it.status = 'error'
        reject(new Error(`状态码 ${xhr.status}`))
      }
    }

    const fd = buildFormData(false, it)
    xhr.send(fd)
  })
}

function reset() {
  if (isUploading.value) return
  files.value = []
  rootFolder.value = ''
  if (!userEditedFolderName.value) folderNameInput.value = ''
  progress.value = { loadedBytes: 0, totalBytes: 0 }
  resetMessages()
}

function removeOne(idx) {
  if (isUploading.value) return
  files.value.splice(idx, 1)
  nextTick(deriveRootFromRelPaths)
}
</script>

<style scoped>
.kb-upload { max-width: 980px; margin: 24px auto; padding: 16px; font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, 'Helvetica Neue', Arial; color: #0f172a; }
h2 { font-size: 20px; margin: 0 0 16px; }

