.picker-row { display: flex; gap: 12px; margin-bottom: 12px; align-items: center; flex-wrap: wrap; }
.btn { display: inline-flex; align-items: center; gap: 8px; padding: 8px 14px; border-radius: 10px; border: 1px solid #cbd5e1; background: #ffffff; cursor: pointer; user-select: none; transition: all .15s ease; }
.btn:hover { box-shadow: 0 1px 6px rgba(0,0,0,.06); transform: translateY(-1px); }
.btn.primary { background: #2563eb; border-color: #1d4ed8; color: #fff; }
.btn.primary:hover { filter: brightness(1.05); }
