.progress { position: relative; height: 12px; background: #e5e7eb; border-radius: 999px; overflow: hidden; margin-top: 6px; }
.progress .bar { height: 100%; background: linear-gradient(90deg, #60a5fa, #3b82f6); width: 0%; transition: width .15s ease; }
