<template>
  <div class="upload-page">
    <div class="card">
      <div class="card-header">
        <h5 class="card-title">{{ category }}上传</h5>
        <div class="header-accent"></div>
      </div>
      <div class="card-body">
        <div class="upload-area" @dragover.prevent @drop.prevent="handleDrop">
          <svg class="upload-icon" viewBox="0 0 24 24">
            <path d="M9,16V10H5L12,3L19,10H15V16H9M5,20V18H19V20H5Z" />
          </svg>
          <input type="file" class="file-input" @change="handleFileUpload" multiple />
          <p class="upload-hint">点击选择文件或拖拽到此区域</p>
          <p class="text-muted">支持多文件上传</p>
        </div>
        <FileList :files="uploadedFiles" @remove="removeFile" />
        <button class="btn-save" @click="saveFiles">
          保存文件
          <span class="save-icon">✓</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import FileList from './FileList.vue';
import axios from 'axios';

export default {
  props: ['category'],
  components: { FileList },
  data() {
    return {
      uploadedFiles: [],
    };
  },
  methods: {
    handleFileUpload(event) {
      const files = event.target.files;
      for (let i = 0; i < files.length; i++) {
        this.uploadedFiles.push(files[i]);
      }
    },
    removeFile(index) {
      this.uploadedFiles.splice(index, 1);
    },
    async saveFiles() {
      if (this.uploadedFiles.length === 0) {
        alert('请先上传文件');
        return;
      }

      const formData = new FormData();

      this.uploadedFiles.forEach(file => {
        formData.append('file', file);
      });

      formData.append('category', this.category);

      try {
        const response = await axios.post('/api/materials/uploadFiles', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });

        console.log('上传成功:', response.data);
        alert('文件已上传');
      } catch (error) {
        console.error('上传失败:', error);
        alert('文件上传失败，请重试');
      }
    },
  },
};
</script>

<style>
.upload-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background:  #f5f7fa;
  padding: 2rem;
}

.card {
  width: 100%;
  max-width: 800px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
}

.card-header {
  position: relative;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 1.5rem;
  text-align: center;
}

.header-accent {
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  height: 10px;
  background: rgba(118, 75, 162, 0.3);
  border-radius: 0 0 50% 50%;
}

.card-title {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 1px;
}

.upload-area {
  border: 2px dashed #c3cfe2;
  border-radius: 12px;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
  margin-bottom: 1.5rem;
  position: relative;
}

.upload-area:hover {
  border-color: #667eea;
  background: rgba(102, 126, 234, 0.05);
}

.file-input {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
}

.upload-icon {
  width: 64px;
  height: 64px;
  fill: #667eea;
  margin-bottom: 1rem;
  opacity: 0.8;
}

.upload-hint {
  color: #2d3748;
  margin: 0.5rem 0;
  font-weight: 500;
}

.btn-save {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 12px 28px;
  border-radius: 8px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 1.5rem auto 0;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.btn-save:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  opacity: 0.95;
}

.save-icon {
  font-size: 1.2em;
  transition: transform 0.2s ease;
}

.btn-save:hover .save-icon {
  transform: scale(1.2);
}

/* 响应式设计 */
@media (max-width: 768px) {
  .card {
    margin: 1rem;
    border-radius: 12px;
  }

  .upload-area {
    padding: 1.5rem;
  }
}
</style>
